import News from '../../controllers/News';

export default class SearchView {
  static clickEvent() {
    News.createNews(`https://newsapi.org/v2/everything?q=${document.getElementsByClassName('search-box')[0].value}&pageSize=40&apiKey=7ac8b9be31d6456dadff95be65e32acf`);
  }

  static createSearchBox() {
    const search = document.createElement('div');
    search.className = 'form';
    const box = document.createElement('input');
    const button = document.createElement('button');
    document.head.insertAdjacentHTML('beforeEnd', '<link rel="stylesheet" href="https://use.fontawesome.com/releases/v5.8.2/css/all.css" integrity="sha384-oS3vJWv+0UjzBfQzYUhtDYW+Pj2yciDJxpsK1OYPAYjqT085Qq/1cq5FLXAZQ7Ay" crossorigin="anonymous">');
    box.className = 'search-box';
    box.placeholder = 'Search...';
    button.className = 'search-btn';
    button.addEventListener('click', SearchView.clickEvent);
    button.insertAdjacentHTML('beforeEnd', '<i class="fas fa-search"></i>');
    box.addEventListener('keyup', (event) => {
      event.preventDefault();
      if (event.keyCode === 13) {
        SearchView.clickEvent();
      }
    });
    search.appendChild(box);
    search.appendChild(button);
    const header = document.body.getElementsByTagName('header')[0];
    header.appendChild(search);
  }
}
