import News from '../../controllers/News';

export default class SourcesView {
  constructor(sources, id) {
    this.sources = sources;
    this.id = id;
  }

  createList() {
    const list = document.createElement('nav');
    const ul = document.createElement('ul');
    ul.className = 'list';
    let li = document.createElement('li');
    li.insertAdjacentHTML('beforeEnd', 'All Sources');
    li.className = 'li';
    li.addEventListener('click', () => { News.createNews('https://newsapi.org/v2/top-headlines?language=en&pageSize=40&apiKey=7ac8b9be31d6456dadff95be65e32acf'); });
    ul.appendChild(li);
    list.className = 'list-box';
    for (let i = 0; i < this.sources.length; i += 1) {
      li = document.createElement('li');
      li.className = 'li';
      li.insertAdjacentHTML('beforeEnd', `${this.sources[i]}`);
      li.addEventListener('click', () => { News.createNews(`https://newsapi.org/v2/everything?sources=${this.id[i]}&pageSize=40&apiKey=7ac8b9be31d6456dadff95be65e32acf`); });
      ul.appendChild(li);
    }
    list.appendChild(ul);
    document.body.insertBefore(list, document.body.getElementsByClassName('news-element')[0]);
  }
}
