import AppModel from '../models/AppModel';
import NewsView from '../views/NewsView';
import SourcesView from '../views/SourcesView';
import News from './News';
import SearchView from '../views/SearchView';

export default class App {
  constructor() {
    this.state = 'https://newsapi.org/v2/sources?apiKey=7ac8b9be31d6456dadff95be65e32acf';
  }

  async start() {
    NewsView.createHeader();
    SearchView.createSearchBox();
    News.createNews('https://newsapi.org/v2/top-headlines?language=en&pageSize=40&apiKey=7ac8b9be31d6456dadff95be65e32acf');
    const data = await AppModel.getNews(this.state);
    const sources = AppModel.getSources(data);
    const id = AppModel.getSourceID(data);
    const sourcesView = new SourcesView(sources, id);
    sourcesView.createList();
    NewsView.createFooter();
  }
}
