export default class NewsView {
  constructor(titles, authers, imgs, descriptions, urls, sources, index) {
    this.titles = titles;
    this.authers = authers;
    this.imgs = imgs;
    this.descriptions = descriptions;
    this.urls = urls;
    this.sources = sources;
    this.index = index;
  }

  render() {
    if (this.titles.length === 0) {
      const result = document.createElement('div');
      result.className = 'result';
      result.insertAdjacentHTML('beforeEnd', '<h2>There are no articles matching your request :(</h2>');
      document.body.insertBefore(result, document.body.lastChild);
    } else {
      for (let i = this.index; (i < (this.index + 5)) && (i < this.titles.length); i += 1) {
        const content = document.createElement('div');
        const descr = document.createElement('div');
        content.className = 'news-element';
        content.addEventListener('click', () => { document.location.href = this.urls[i]; });
        if (this.imgs[i] !== null) {
          content.insertAdjacentHTML('beforeEnd', `<img src="${this.imgs[i]}">`);
        } else {
          content.insertAdjacentHTML('beforeEnd', '<img src="http://dengi.bm.img.com.ua/dengi/orig/9/68/df958bc1d296d0bce3981c9798d6c689.jpg">');
        }
        descr.className = 'news-description';
        descr.insertAdjacentHTML('beforeEnd', `<h2>${this.titles[i]}</h2>`);
        if (this.sources[i] !== null) {
          descr.insertAdjacentHTML('beforeEnd', `<span>${this.sources[i]}</span>`);
        }
        if (this.descriptions[i] !== null) {
          descr.insertAdjacentHTML('beforeEnd', `<p>${this.descriptions[i]}</p>`);
        }
        content.appendChild(descr);
        document.body.insertBefore(content, document.body.getElementsByClassName('load-more')[0]);
      }
      this.index += 5;
    }
  }

  createButton() {
    const button = document.createElement('a');
    button.className = 'load-more';
    button.insertAdjacentHTML('beforeEnd', '<span>Load More</span>');
    button.addEventListener('click', () => {
      this.render();
      if (this.index >= 40) {
        button.parentNode.removeChild(button);
      }
    });
    document.body.insertBefore(button, document.body.getElementsByTagName('footer')[0]);
  }

  static createFooter() {
    const footer = document.createElement('footer');
    footer.insertAdjacentHTML('beforeEnd', '<p>Copiright <a href="https://newsapi.org/">NewsAPI</a></p>');
    document.body.appendChild(footer);
  }

  static createHeader() {
    const header = document.createElement('header');
    header.insertAdjacentHTML('beforeEnd', '<h1>JS-news-parser 751007</h1>');
    document.body.appendChild(header);
  }

  static deleteAll() {
    let nodes = document.body.getElementsByClassName('news-element');
    while (nodes[0]) {
      nodes[0].parentNode.removeChild(nodes[0]);
    }
    nodes = document.body.getElementsByClassName('load-more');
    if (nodes[0]) {
      nodes[0].parentNode.removeChild(nodes[0]);
    }
    nodes = document.body.getElementsByClassName('result');
    if (nodes[0]) {
      nodes[0].parentNode.removeChild(nodes[0]);
    }
  }
}
