export default class AppModel {
  constructor(state) {
    this.state = state;
  }

  static async getNews(url) {
    const responce = await fetch(url);
    const data = await responce.json();

    return data;
  }

  static getNewsTitles(data) {
    return data.articles.map(article => article.title);
  }

  static getNewsAuthors(data) {
    return data.articles.map(article => article.author);
  }

  static getNewsSource(data) {
    return data.articles.map(article => article.source.name);
  }

  static getNewsImgs(data) {
    return data.articles.map(article => article.urlToImage);
  }

  static getNewsDescriptions(data) {
    return data.articles.map(article => article.description);
  }

  static getNewsURLs(data) {
    return data.articles.map(article => article.url);
  }

  static getSources(data) {
    return data.sources.map(source => source.name);
  }

  static getSourceID(data) {
    return data.sources.map(source => source.id);
  }
}
