import AppModel from '../models/AppModel';
import NewsView from '../views/NewsView';

export default class News {
  static async createNews(url) {
    NewsView.deleteAll();
    const data = await AppModel.getNews(url);
    const titles = AppModel.getNewsTitles(data);
    const authers = AppModel.getNewsAuthors(data);
    const imgs = AppModel.getNewsImgs(data);
    const descriptions = AppModel.getNewsDescriptions(data);
    const urls = AppModel.getNewsURLs(data);
    const newsSource = AppModel.getNewsSource(data);
    const view = new NewsView(titles, authers, imgs, descriptions, urls, newsSource, 0);
    if (titles.length !== 0) {
      view.createButton();
    }
    view.render();
  }
}
